<template>
    <div class="container-wrapper">

        <!-- 标题 -->
        <h1 class="title">
            我的账户
            <span class="line">/</span>
            <span class="">Account</span>
        </h1>

        <!-- 内容部分 -->
        <div class="main-content">
            
            <!-- 左边侧栏 -->
            <ul class="slide-nav">
                <li class="item" v-for="(item, index) in slideNavs" :key="index">
                    <router-link :to="item.url">{{ item.text }}</router-link>
                </li>
            </ul>

            <!-- 内容卡片 -->
            <div class="content">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                slideNavs: [
                    // { url: "/account/purchased", text: "已购作品" },
                    // { url: "/account/balance", text: "账户余额" },
                    // { url: "/account/binding", text: "账户绑定" },
                    { url: "/account/certify", text: "艺术家认证" },
                    { url: "/account/upgrade", text: "升级为企业号" }
                ]
            }
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>

    .container-wrapper {
        width: 1000px;
        min-width: 800px;
        margin: 0 auto;
    }

    // 标题
    .title {
        font-size: large;
        margin-top: 20px;
        span {
            display: inline-block;
            color: #b1b1b1;
            font-size: medium;
        }
        span.line {
            color: #48abff;
        }
    }

    // 内容部分
    .main-content {
        margin-top: 30px;
        position: relative;

        // 侧边导航栏
        .slide-nav {
            width: 150px; 
            position: absolute; 
            top: 0; 
            left: 0;
            background: #f2f2f2;
            padding: 10px 0;
            border-radius: 3px;
            .item {
                height: 40px;
                line-height: 3;
                text-align: center;
                font: {
                    size: small;
                    weight: bold;
                }
                a {
                    color: #444;
                }
                &:hover {
                    background: #48abff;
                    a {
                        color: #fff;
                    }
                }
            }
        }

        // 路由高亮显示
        .router-link-exact-active {
            background: #48abff;
            color: #fff !important;
        }

        .content {
            margin-left: 170px;
            background: #fbfbfb;
            padding: 20px;
        }
    }
</style>


